                     @click=\"SelectMenu(item.text)\"\r\n                        >\r\n                            <v-list-item-icon>\r\n                                <v-icon v-text=\"item.icon\"></v-icon>\r\n                            </v-list-item-icon>\r\n                            <v-list-item-content>\r\n                                <v-list-item-title\r\n                                    v-text=\"item.text\"\r\n                                ></v-list-item-title>\r\n                            </v-list-item-content>\r\n                        </v-list-item>\r\n                    </v-list-item-group>\r\n                </v-list>\r\n            </v-menu>\r\n        </v-app-bar>\r\n\r\n        <v-main class=\"main-wrapper\">\r\n            <slot />\r\n        </v-main>\r\n\r\n        <ReLogin :user=\"user\"  :dialog=\"dialog_relogin\"  @dialog=\"dialog_relogin = $event\" />\r\n    </v-app>\r\n</template>\r\n\r\n<script>\r\nimport ReLogin from \"../components/ReLogin.vue\";\r\nexport default {\r\n    components: { ReLogin },\r\n    data: () => ({\r\n        dialog_relogin: false,\r\n\r\n        items: [\r\n            { text: \"Perfil\", icon: \"mdi-account\" },\r\n            { text: \"Configuracion\", icon: \"mdi-cog\" },\r\n            { text: \"Salir\", icon: \"mdi-power\" },\r\n        ],\r\n        selectedMenu: null,\r\n        focus: false,\r\n        blur: false,\r\n        session: true,\r\n    }),\r\n    computed: {\r\n        user() {\r\n            return this.$page.props.auth.user;\r\n        },\r\n    },\r\n    methods: {\r\n        SelectMenu(menu) {\r\n            if (menu == \"Perfil\") {\r\n\r\n            } else if (menu == \"Configuracion\") {\r\n\r\n            } else if (menu == \"Salir\") {\r\n                this.$inertia.post(\"/logout\");\r\n            }\r\n        },\r\n    },\r\n    mounted() {\r\n        window.addEventListener(\"focus\", () => {\r\n            this.focus = new Date();\r\n            console.log(\"focus\");\r\n            if (!this.blur) return;\r\n            let diff = this.focus - this.blur;\r\n            diff = diff / 1000 / 60;\r\n            console.log(diff);\r\n            if (diff > 60) {\r\n                this.session = false;\r\n                this.dialog_relogin = true;\r\n                //comprobar si hay sesion activa\r\n                //mostrar ReLogin\r\n            }\r\n        });\r\n        window.addEventListener(\"blur\", () => {\r\n            this.blur = new Date();\r\n            console.log(\"salio: \" + this.blur);\r\n        });\r\n    },\r\n};\r\n</script>\r\n\r\n<style>\r\n@import url(\"https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap\");\r\n\r\n* {\r\n    font-family: \"Montserrat\", sans-serif;\r\n}\r\n.main-wrapper {\r\n    background-color: #eaeaea;\r\n}\r\n\r\n.v-btn:not(.v-btn--round).v-size--default {\r\n    height: 40px;\r\n    min-width: 64px;\r\n    padding: 0 16px;\r\n}\r\n\r\n.v-messages {\r\n    flex: 1 1 auto;\r\n    font-size: 10px;\r\n    height: 12px;\r\n    min-width: 1px;\r\n    position: relative;\r\n}\r\n\r\n.v-text-field__details {\r\n    display: flex;\r\n    flex: 1 0 auto;\r\n    max-width: 100%;\r\n    max-height: 12px;\r\n    overflow: hidden;\r\n}\r\n\r\n.v-text-field.v-text-field--enclosed .v-text-field__details {\r\n    padding-top: 0;\r\n    margin-bottom: 0px;\r\n}\r\n\r\n.v-text-field--outlined fieldset {\r\n    border: 1px solid #d4d4d8;\r\n}\r\n\r\n.v-text-field .v-input__control,\r\n.v-text-field .v-input__slot,\r\n.v-text-field fieldset {\r\n    border-radius: 0.375rem;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
